"use client";

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

/* -------------------------------------------------------
   Crash Watch Panel ‚Äî Global Country Risk Scores
------------------------------------------------------- */
interface CountryRisk {
  code: string;          // ISO alpha-2
  name: string;
  risk: number;          // 0‚Äì100
  color: string;         // bg class
}

export default function CrashWatch() {
  const router = useRouter();
  const [countries, setCountries] = useState<CountryRisk[]>([]);

  /* ----------  Load fake demo data  ---------- */
  useEffect(() => {
    const seed: CountryRisk[] = [
      { code: "us", name: "United States", risk: 82, color: "bg-red-600" },
      { code: "cn", name: "China",         risk: 74, color: "bg-red-500" },
      { code: "de", name: "Germany",       risk: 61, color: "bg-amber-500" },
      { code: "br", name: "Brazil",        risk: 49, color: "bg-amber-400" },
      { code: "in", name: "India",         risk: 36, color: "bg-blue-400" },
      { code: "jp", name: "Japan",         risk: 22, color: "bg-blue-500" },
      { code: "za", name: "South Africa",  risk: 18, color: "bg-blue-600" },
    ];
    setCountries(seed);
  }, []);

  /* ----------  Click handler  ---------- */
  const handleSelect = (c: CountryRisk) => {
    router.push(`/intel/${c.code.toLowerCase()}`);
  };

  return (
    <div className="rounded-lg border bg-white p-3">
      <h3 className="text-xs uppercase tracking-wider text-slate-500 mb-2">
        Crash Watch
      </h3>

      <div className="max-h-[300px] overflow-y-auto space-y-2">
        {countries.map((c) => (
          <button
            key={c.code}
            onClick={() => handleSelect(c)}
            className="flex items-center justify-between w-full border rounded-md px-2 py-2 hover:bg-slate-50 transition"
          >
            <div className="flex items-center gap-2 text-left">
              <span className={`fi fi-${c.code.toLowerCase()} fis`} />
              <span className="text-sm font-medium">{c.name}</span>
            </div>
            <div className="flex items-center gap-2">
              <div
                className={`w-2 h-2 rounded-full ${c.color} animate-pulse`}
                title={`${c.risk}% risk`}
              />
              <span className="text-xs text-slate-500 font-semibold w-8 text-right">
                {c.risk}%
              </span>
            </div>
          </button>
        ))}
      </div>

      <p className="text-[11px] text-slate-400 mt-2">
        ‚ö† Higher % = elevated market risk ¬∑ Data auto-refresh hourly
      </p>
    </div>
  );
}
