const TODAY = new Date();
function addDays(d: Date, n: number) { const c = new Date(d); c.setDate(c.getDate()+n); return c; }
function fmtDate(d: Date) { return d.toLocaleDateString(undefined, { month:"short", day:"numeric", year:"numeric" }); }
function fmtISO(d: Date) { return d.toISOString().slice(0,10); }
function readNum(qs: URLSearchParams, k: string, def: number) {
  const v = Number(qs.get(k)); return Number.isFinite(v) ? v : def;
}
